// 서브페이지
// 농장에서 우리 손으로 component - sub_Farm_stroy.jsx
.farm_story {
  width: 100%;
  .part1_wrap {
    margin-bottom: 80px;
    .part1 {
      max-width: 1100px;
      margin: 0 auto;
      .part1_title {
        width: 100%;
        margin-bottom: 10px;
        h3 {
          font-weight: 400;
        }
      }
      .part1_contents {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
        img {
          flex: 1;
        }
        .part1_text {
          flex: 1;
          h3 {
            font-style: italic;
            font-weight: 200;
            color: rgb(0, 59, 8);
            margin-bottom: 20px;
          }
          p {
            line-height: 25px;
            font-size: 14px;
            font-weight: 300;
          }
        }
      }
    }
  }

  .part2_wrap {
    background-color: #f4f4f2;
    padding: 50px 0;
    margin-bottom: 80px;
