// 서브페이지
// 로스트스펙트럼 component - farm_spectrum.jsx
.farm_spectrum {
  width: 100%;
  .part1_wrap {
    margin-bottom: 80px;
    .part1 {
      max-width: 1100px;
      margin: 0 auto;
      .part1_title {
        width: 100%;
        margin-bottom: 10px;
        h3 {
          font-weight: 400;
        }
      }
      .part1_contents {
        .part1_text {
          padding: 20px 20px;
          h3 {
            font-style: italic;
            font-weight: 400;
            color: rgb(0, 59, 8);
            margin-bottom: 20px;
          }
          p {
            color: #333;
            line-height: 25px;
            font-size: 14px;
            font-weight: 300;
          }
        }
      }
    }
  }

  .part2_wrap {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 30px;
    max-width: 1100px;
    margin: 0 auto;
    margin-bottom: 80px;
    overflow: hidden;
    .part2 {
      flex: 1;
      margin: 0 auto;
      .part2_title {
        width: 100%;
        margin-bottom: 10px;
        h3 {
          font-weight: 400;
        }
      }
      .part2_contents {
        display: flex;
        justify-content: center;
        flex-direction: column;
        .part2_img {
          display: flex;
          flex-direction: column;
          text-align: center;
          img {
            width: 300px;
          }
          p {
            width: 300px;
            text-align: left;
          }
        }
        .part2_text {
          padding: 20px 40px 20px 0;
          h5 {
            font-weight: 400;
            color: rgb(0, 59, 8);
            margin-bottom: 20px;
          }
          p {
            color: #333;
            line-height: 25px;
            font-size: 14px;
            font-weight: 300;
          }
        }
      }
      &:nth-child(1) {
        .part2_img {
          p {
            background-color: #be8e24;
          }
        }
      }
      &:nth-child(2) {
        .part2_img {
          p {
            background-color: #8a4b24;
          }
        }
      }
      &:nth-child(3) {
        .part2_img {
          p {
            background-color: #8a6064;
          }
        }
      }
    }
  }
}

