.cappuccino {
  width: 100%;
  margin: 0 auto;
  .cappuccino_title {
    position: relative;
    max-width: 1100px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 40px;
    border-bottom: 1px solid rgb(0, 59, 8);
    h2 {
      display: inline-block;
      width: 70%;
      padding: 20px 0;
      margin: 0 auto;
      text-align: left;
    }
    .section_tag {
      display: inline-block;
      position: relative;
      top: 18px;
      margin: 0 20px;
    }
  }
  .part1_wrap {
    max-width: 1100px;
    margin: 0 auto;
    .part1_contents {
      margin-bottom: 40px;
      img {
      }
      .part1_text {
        margin-top: 20px;
        h3 {
          font-style: italic;
          font-weight: 200;
          color: rgb(0, 59, 8);
        }
        p {
          margin-top: 20px;
          font-weight: 200;
        }
      }
    }
  }

  .part2_wrap {
    width: 100%;
    padding: 40px 0;
    background-color: #f4f4f2;
    .part2_contents {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 30px;
      max-width: 1100px;
      margin: 40px auto;
      .part2_img {
        width: 200px;
        height: 200px;
        overflow: hidden;
        img {
          width: 200px;
          height: 200px;
          transition: 0.5s;
          &:hover {
            transform: scale(1.05);
          }
        }
      }
      .part2_text {
        flex: 8;
        h4 {
          font-style: italic;
          font-weight: 200;
          color: rgb(0, 59, 8);
        }
        p {
          margin-top: 20px;
          font-weight: 200;
        }
      }
    }
  }
}
