:root {
  --headercolor: rgba(253, 249, 242);
  --mcolor: #007043;
  --scolor: #003d25;
  --sscolor: #22644a;
  --fontcolor: #333;
  font-family: "나눔바른고딕OTF", "맑은 고딕", Arial, Helvetica, sans-serif;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}

ul,
ol,
li {
  list-style: none;
}

a {
  display: block;
  text-decoration: none;
  color: #333;

}