// 농장이야기
.story_farm_wrap {
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
  .story_farm_title {
    position: relative;
    max-width: 1100px;
    margin: 0 auto;
    margin-top: 20px;
    border-bottom: 1px solid rgb(0, 59, 8);
    h2 {
      display: inline-block;
      width: 70%;
      padding: 20px 0;
      margin: 0 auto;
      text-align: left;
    }
    .section_tag {
      display: inline-block;
      position: relative;
      top: 18px;
      margin: 0 20px;
    }
  }

  .story_farm_menu {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin: 20px 0;
    padding: 40px;
    li {
      width: 200px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #fff;
      border-radius: 5px;
      background-color: #aaa;
      cursor: pointer;
      transition: 0.3s;
      &.on {
        background-color: rgb(0, 59, 8);
        a {
          color: #fff;
        }
      }
      a {
        color: #fff;
      }
    }
  }

}
