.best_common {
  width: 100%;
  .best_common_wrap {
    width: 1100px;
    margin: 0 auto;
    .best_common_title {
      display: flex;
      align-items: center;
      height: 100px;
      h2 {
        padding-top: 10px;
        margin-left: 20px;
      }
    }
    .video {
      width: 1100px;
      margin-bottom: 80px;
    }
  }

  .best_common_contents {
    width: 100%;
    background-color: #f4f4f2;
    .best_common_content {
      width: 1100px;
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      gap: 40px;
      padding: 80px 0;
      .item {
        flex: 1;
        display: flex;
        align-items: center;
        // gap: 30px;
        overflow: hidden;
        @for $i from 1 through 10 {
          &:nth-child(#{$i}) .item_text {
            &::after {
              position: absolute;
              top: -10px;
              left: -60px;
              content: "#{$i}";
              display: block;
              width: 50px;
              height: 100px;
              font-size: 80px;
              color: #ccc;
              text-align: right;
            }
          }
        }

        .item_img {
          width: 40%;
          margin-right: 80px;
          overflow: hidden;
          img {
            width: 100%;
            transition: 0.5s;
            &:hover {
              transform: scale(1.05);
            }
          }
        }
        .item_text {
          width: 60%;
          position: relative;
          h3 {
            color: rgb(0, 59, 8);
            font-weight: 200;
            font-style: italic;
            margin-bottom: 20px;
          }
          p {
            font-weight: 200;
          }
        }
      }
    }
  }
}
