@mixin flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

#header {
  position: relative;
  top: 0;
  width: 100%;
  height: 100px;
  border-bottom: 2px solid #333;
  background-color: var(--headercolor);
  z-index: 999;
  .header_wrap {
    display: flex;
    justify-content: center;
    width: 65%;
    margin: 0 auto;
    .logo {
      flex-grow: 1;
      height: 100px;
      padding: 10px 0;
      text-align: center;
      box-sizing: border-box;
      img {
        height: 80px;
      }
    }
    //헤더 메뉴
    .main_menu_wrap {
      position: relative;
      flex-grow: 9;
      height: 100px;
      ul.main_gnb {
        display: flex;
        gap: 20px;
        position: absolute;
        right: 0;
        height: 50px;
        li {
          position: relative;
          min-width: 50px;
          line-height: 50px;
          text-align: right;
          font-weight: 200;
          font-size: 12px;
          &::after {
            position: absolute;
            left: 65px;
            top: 0;
            content: "|";
          }
          &:last-child {
            form {
              position: relative;
              input {
                width: 30px;
                height: 30px;
                background-color: rgb(255, 255, 255, 0.3);
                padding: 0 15px;
                transition: 0.5s;
                border: 1px solid rgb(0, 59, 8);
                border-radius: 10px;
                box-sizing: border-box;
                &:focus {
                  width: 200px;
                }
              }
              label {
                img {
                  position: absolute;
                  top: -1px;
                  bottom: 2px;
                  right: 6px;
                  margin: auto;
                  height: 20px;
                }
              }
            }
            &::after {
              content: "";
            }
          }
        }
      }

      ul.main_menu {
        display: flex;
        position: absolute;
        bottom: -2px;
        right: 0;
        height: 50px;
        > li {
          min-width: 120px;
          height: 50px;
          line-height: 50px;
          text-align: center;
          font-weight: 400;
          font-size: 14px;
          &:hover {
            border-top-right-radius: 5px;
            border-top-left-radius: 5px;
            background-color: #333;
            > a {
              color: #fff;
            }
          }
          &:hover ul.sub_menu {
            max-height: 500px;
          }
          > a {
            color: #333;
          }
          ul.sub_menu {
            position: absolute;
            top: 50px;
            left: 0px;
            width: 600px;
            max-height: 0;
            overflow: hidden;
            background-color: var(--headercolor);
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            transition: 0.3s;
            li {
              display: flex;
              flex-wrap: wrap;
              width: 100%;
              a {
                width: 20%;
                height: 30px;
                line-height: 30px;
                padding: 5px 0;
                color: #333;
                transition: 0.5s;
                &:hover {
                  color: #fff;
                  background-color: rgb(0, 59, 8);
                }
              }
            }
          }
        }
      }
    }
  }
}

#footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  background-color: #ededed;
  .footer_top {
    width: 100%;
    height: 300px;
    ul.footer_menu {
      display: flex;
      justify-content: center;
      width: 100%;
      height: 100%;
      text-align: center;
      > li {
        width: 200px;
        height: 40px;
        > a {
          color: #333;
          font-size: 18x;
          line-height: 40px;
        }
        ul.footer_sub_menu {
          li {
            width: 100%;
            height: 25px;
            a {
              color: #777;
              font-size: 14px;
              line-height: 25px;
            }
          }
        }
      }
    }
  }
  .footer_bottom {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 200px;
    ul.footer_bottom_menu {
      width: 1200px;
      margin: 0 auto;
      li {
        display: flex;
        margin-top: 30px;
        box-sizing: border-box;
        a {
          font-size: 12px;
          padding-right: 10px;
          border-right: 1px solid #333;
          margin-right: 10px;
          &:last-child {
            margin-right: 0;
            border-right: none;
          }
        }
      }
    }
    .footer_bottom_btn_wrap {
      display: flex;
      justify-content: center;
      gap: 10px;
      .btn {
        margin-top: 30px;
        font-size: 12px;
        color: #333;
        &:hover {
          color: #fff;
        }
      }
    }
  }
}
