.starbucksbean {
  width: 1100px;
  margin: 0 auto;
  .starbucksbean_title {
    position: relative;
    width: 100%;
    margin: auto;
    margin-top: 20px;
    border-bottom: 1px solid rgb(0, 59, 8);
    h2 {
      display: inline-block;
      width: 70%;
      padding: 20px 0;
      margin: 0 auto;
      text-align: left;
    }
    .section_tag {
      display: inline-block;
      position: relative;
      top: 18px;
      margin: 0 20px;
    }
  }
  .starbucksbean_index {
    max-width: 1100px;
    margin: 0 auto;
    .starbucksbean_index_inner {
      width: 90%;
      border-radius: 10px;
      padding: 40px;
      margin: 20px 0;
      h2 {
        margin-bottom: 40px;
        text-align: left;
      }
      .beanList {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        li {
          width: 200px;
          height: 30px;
          line-height: 30px;
          text-align: center;
          color: #fff;
          border-radius: 5px;
          background-color: #aaa;
          cursor: pointer;
          transition: 0.3s;
          &.on {
            background-color: rgb(0, 59, 8);
          }
        }
      }
    }
  }

  .starbucksbean_list {
    width: 1100px;
    margin: 0 auto;
    .startbucksbean_list_inner {
      width: 90%;
      padding: 0 20px;
      transition: 1s;
      > .startbucksbean_list_inner_items {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        gap: 15px;
        width: 100%;
        margin-bottom: 80px;
        .startbucksbean_list_inner_items_title {
          width: 100%;
          height: 50px;
          line-height: 50px;
          // margin-top: 40px;
          border-top-left-radius: 20px;
          border-bottom-right-radius: 20px;
          background-color: #eee;
          h2 {
            color: rgb(0, 59, 8);
            margin-left: 20px;
            font-weight: 400;
          }
        }
        .bean_item {
          > div {
            flex: 1;
            img {
              margin: 20px;
              height: 200px;
              transition: 0.5s;
              cursor: pointer;
              &:hover {
                transform: scale(1.1);
              }
            }
            div {
              text-align: center;
              font-size: 14px;
              font-weight: 200;
              color: #555;
            }
          }
        }
      }
    }
  }
}
