// 드라이브스루
.driveThrough {
  position: relative;
  max-width: 1100px;
  margin: 0 auto;
  .drive_title {
    width: 100%;
    height: 100px;
    margin-bottom: 40px;
    line-height: 130px;
    border-bottom: 1px solid #333;
    box-sizing: border-box;
    h2 {
      display: inline-block;
      color: #333;
    }
    .section_tag {
      position: relative;
      top: 18px;
      margin: 0 20px;
      img {
      }
    }
  }
  .driveimg {
    text-align: center;
    img {
      width: 100%;
    }
    p {
      text-align: left;
      &:nth-child(2) {
        margin-top: 20px;
        font-size: 20px;
        color: rgb(0, 59, 8);
        strong {
        }
      }
      &:nth-child(3) {
        font-size: 14px;
        margin-bottom: 40px;
      }
    }
  }
  .drivehow {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 600px;
    margin-bottom: 40px;
    > div {
      position: absolute;
      width: 100%;
      height: 500px;
      &.drive_bg {
        background: url("../../image/drivethrough/drive_through_bg.jpg") center;
        > div {
          position: absolute;
          width: 150px;
          height: 150px;
          z-index: 3;
          animation: bounce 2s linear infinite;
          &:nth-child(1) {
            top: 100px;
            left: 25%;
          }
          &:nth-child(2) {
            top: 50px;
            left: 60%;
            animation-delay: 0.2s;
          }
          &:nth-child(3) {
            top: 270px;
            left: 40%;
            animation-delay: 0.4s;
          }
          @keyframes bounce {
            0% {
              transform: translateY(-10px);
            }
            50% {
              transform: translateY(0px);
            }
            100% {
              transform: translateY(-10px);
            }
          }
          img {
            width: 150px;
            height: 150px;
            filter: drop-shadow(0 5px 10px rgb(0, 59, 8));
          }
          p {
            position: absolute;
            width: 250px;
            top: 30px;
            left: 150px;
            font-size: 14px;
            color: rgb(0, 59, 8);
          }
        }
      }
      &.drive_rail {
        background: url("../../image/drivethrough/drive_through_rail2.png")
          no-repeat center;
        background-size: contain;
      }
      &.drive_car {
        position: relative;
        width: 100%;
        height: 500px;
        overflow: hidden;
        img {
          position: absolute;
          width: 50px;
          height: 50px;
          offset: path(
            "M119-49c0,0-109,226,104,228s684,0,684,0s70,6,70,79s-81,83-81,83H180c0,0-78,10-78,104s0,101,0,101"
          );
          animation: carmove 15s ease-in infinite;
          animation-delay: 2s;
          z-index: 2;
        }
        @keyframes carmove {
          0% {
            offset-distance: 0%;
          }
          100% {
            offset-distance: 100%;
          }
        }
      }
    }
  }

  .didyouKnow {
    position: relative;
    max-width: 1100px;
    height: 390px;
    margin: 0 auto;
    .didyouimg {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      img {
        width: 550px;
        height: 260px;
      }
    }
    .didyoutext {
      position: absolute;
      top: 0;
      left: 560px;
      p {
        font-size: 14px;
        font-weight: 200;
        &:nth-child(1) {
          margin: 20px 0;
          font-weight: bold;
          text-shadow: 0 0 3px rgba(5, 5, 5, 0.3);
        }
        &:nth-child(3) {
        }
      }
      h3 {
        margin: 20px 0;
        color: rgb(0, 59, 8);
      }
    }
  }
  .find_through {
    position: absolute;
    bottom: 40px;
    left: 0;
    width: 100%;
    height: 50px;
    background: rgb(0, 59, 8);
    a {
      p {
        text-align: center;
        color: #fff;
        line-height: 50px;
      }
    }
  }
}

