.hidden {
  display: none;
}

.strong {
  color: rgb(0, 59, 8);
  font-weight: bold;
}

.btn {
  width: 80px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-radius: 5px;
  border: 1px solid rgb(0, 59, 8);
  box-sizing: border-box;
  transition: 0.5s;
  &:hover {
    color: #fff;
    background-color: rgb(0, 59, 8);
  }
}

.App {
  width: 100%;
  overflow: hidden;
}

#map {
  width: 100%;
  height: 500px;
}
