@mixin flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
#main {
  width: 100%;
  .banner {
    width: 100%;
    height: 600px;
    background: var(--mcolor);
    .swiper_ul {
      position: relative;
      width: 100%;
      height: 100%;
      .swiper_li {
        width: 100%;
        height: 100%;
        background-size: cover;
        cursor: pointer;
        a {
          position: relative;
          width: 100%;
          height: 100%;
          img {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            width: 100%;
            height: 100%;
            margin: auto;
            object-fit: cover;
          }
        }
      }
      .swiper-pagination {
        .swiper-pagination-bullet {
          opacity: 1;
          background-color: #fff;
        }
        .swiper-pagination-bullet-active {
          background-color: rgb(0, 59, 8);
        }
      }
    }
  }

  .main_notice_wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 50px;
    background: linear-gradient(
      90deg,
      rgb(0, 59, 8) 50%,
      rgb(255, 255, 255) 50%
    );
    .main_notice {
      display: flex;
      width: 35%;
      height: 50px;
      line-height: 50px;
      text-align: center;
      .main_notice_title {
        flex: 2;
        padding: 0 10px;
        a {
          color: #fff;
        }
      }
      .main_notice_contents {
        flex: 8;
        text-align: center;
        line-height: 50px;
        padding: 0 10px;
        color: #fff;
      }
    }
    .main_promo {
      display: flex;
      width: 35%;
      height: 50px;
      line-height: 50px;
      text-align: center;
      .main_promo_title {
        flex: 3;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
        a {
          // position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 20px;
          line-height: 50px;
          color: rgb(0, 59, 8);
          .toggle_btn {
            position: relative;
            width: 30px;
            height: 30px;
            border: 1px solid #555;
            border-radius: 50%;
            img {
              position: absolute;
              inset: 0;
              margin: auto;
              transition: 0.5s;
              &.up {
                transform: rotate(180deg);
              }
              &.down {
                transform: rotate(0deg);
              }
            }
          }
        }
      }
    }
  }
  .subPromo {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 0px;
    background-color: #eee;

    transition: 0.5s;
    text-align: center;
    overflow: hidden;
    &.active {
      // padding-top: 50px;
      height: 350px;
    }
    .promo_wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 1200px;
      height: 100%;
      margin: 0 auto;
    }
    .promo_img {
      img {
        height: 200px;
      }
    }
    .btn {
      margin-top: 20px;
    }
  }

  .reward {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 300px;
    .reward_wrap {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 1200px;
      height: 100%;
      .reward_logo {
        width: 20%;
        height: 100%;
        text-align: center;
        padding-top: 100px;
        box-sizing: border-box;
        img {
          height: 100px;
        }
      }
      .reward_contents {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 50%;
        height: 100%;
        .reward_title {
          width: 100%;
          h2 {
            text-align: left;
          }
        }
        .reward_content {
          display: flex;
          align-items: center;
          flex-direction: row;
          width: 100%;
          height: 100px;
          box-sizing: border-box;
          &:nth-child(2) {
            border-bottom: 1px solid rgb(0, 59, 8);
          }
          > div {
            flex: 7;
            font-size: 15px;
          }
          .login_signin {
            flex: 3;
            display: flex;
            flex-direction: row;
            .btn {
              color: #000;
              &:hover {
                color: #fff;
              }
              &:nth-child(1) {
                margin-right: 10px;
              }
            }
          }
          .gifcard {
            flex: 3;
            width: 170px;
            height: 30px;
            .btn {
              width: 170px;
              color: #000;
              &:hover {
                color: #fff;
              }
            }
          }
        }
      }
    }
  }
  .newProduct {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 500px;
    // background-color: #eee;
    background: url("../image/coffe/coffebean_bg.jpg") no-repeat center;
    background-size: cover;
    .newProduct_wrap {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      width: 800px;
      height: 100%;
      .newProduct_img {
        flex: 5;
        height: 300px;
        text-align: center;
        img {
          position: absolute;
          top: 0;
          bottom: 0;
          margin: auto;
          transition: 1s;
        }
      }
      .newProduct_contents {
        flex: 5;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        position: absolute;
        transition: 1s;
        transition-delay: 0.3s;
        .newProduct_content {
          width: 100%;
          height: 150px;
          p {
            line-height: 20px;
          }
        }
        a {
          color: #555;
          align-items: flex-start;
          &:hover {
            color: #fff;
          }
        }
      }
    }
  }
  .pick_favorite {
    width: 100%;
    height: 800px;
    background: url("../image//common/background.jpg") no-repeat left;
    background-attachment: fixed;
    background-size: cover;
    .favorite_wrap {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row-reverse;
      position: relative;
      width: 1100px;
      height: 100%;
      margin: 0 auto;
      .img_wrap {
        position: absolute;
        top: 300px;
        bottom: 0;
        right: 0;
        margin: auto;
        width: 50%;
        height: 300px;
        transition: 1s;
        img {
          position: absolute;
          height: 300px;
          &:nth-child(1) {
            top: -300px;
            left: 0;
          }
          &:nth-child(2) {
            top: -200px;
            left: 100px;
          }
          &:nth-child(3) {
            top: 000px;
            left: 200px;
          }
        }
      }
      .favorite_contents {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        width: 50%;
        height: 300px;
        transition: 1s;
        transition-delay: 0.5s;
        p {
          color: #fff;
          text-align: right;
          &:nth-child(1) {
            font-size: 55px;
            font-weight: bold;
            letter-spacing: 4px;
          }
          &:nth-child(2) {
            font-size: 50px;
            font-weight: bold;
            letter-spacing: 1px;
          }
          &:nth-child(3) {
            font-size: 30px;
            font-weight: bold;
            text-transform: uppercase;
          }
          &:nth-child(4) {
            margin-top: 10px;
          }
          &:nth-child(5) {
            margin-top: 20px;
            letter-spacing: 1px;
          }
        }
      }
    }
  }
  .find_store {
    width: 100%;
    height: 500px;
    // background-color: coral;
    .store_wrap {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 1100px;
      height: 100%;
      margin: 0 auto;
      text-align: center;
      .store_img {
        flex: 1;
      }
      .store_text {
        flex: 1;
        h2 {
          color: #000;
          text-align: left;
          margin-bottom: 50px;
          font-weight: 400;
          strong {
            color: coral;
          }
        }
        p {
          color: #000;
          text-align: right;
          strong {
            color: coral;
            a {
              display: inline-block;
              color: coral;
            }
          }
        }
      }
    }
  }
}

#detail {
  width: 100%;
  height: 500px;
  .detail_wrap {
    // display: flex;
    // justify-content: center;
    // align-items: center;
    width: 1200px;
    height: 100%;
    margin: 0 auto;
    .item {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      .item_img {
        margin-top: 100px;
        img {
          height: 200px;
        }
      }
      .item_title {
        margin-top: 25px;
      }
      .count_num {
        // display: flex;
        // justify-content: center;
        // align-items: center;
        .btn_wrap {
          display: flex;
          justify-content: center;
          align-items: center;
          button {
            width: 30px;
            height: 30px;
            color: #fff;
            text-align: center;
            line-height: 30px;
            font-size: 20px;
            border-radius: 50%;
            background: rgb(0, 59, 8);
          }
          .item_num {
            margin: 0 20px;
          }
        }
        #submit_btn {
          margin-top: 10px;
          width: 170px;
          height: 30px;
        }
      }
    }
  }
}

.itemList {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  gap: 30px;
  width: 100%;
  height: 300px;
  background-color: #f4f4f2;
  h2 {
    width: 100%;
    color: rgb(0, 59, 8);
    text-align: center;
  }
  .item {
    width: 100px;
    height: 100px;
    text-align: center;
    img {
      height: 100px;
    }
    div {
    }
    div:nth-child(2) {
      text-align: center;
      font-size: 14px;
      color: rgb(0, 59, 8);
    }
    div:nth-child(3) {
    }
  }
}
